import React, { Component } from 'react'
import { graphql } from 'gatsby'
import styled, {css, keyframes} from 'styled-components'


import Layout from '../components/Layout'
import BlogCard from '../components/BlogCard'
import SearchBar from '../components/SearchBar'
import Cube from '../components/Cube'
import Card from '../components/Card/index'

import bg from '../../static/assets/1451846704872-4c2a8e1468fe.jpg'
import {Link} from 'gatsby'
const sizes = {
  desktop: 992,
  tablet: 768,
  phone: 576,
}
const media = Object.keys(sizes).reduce((acc, label) => {
  acc[label] = (...args) => css`
    @media (max-width: ${sizes[label] / 16}em) {
      ${css(...args)}
    }
  `
  return acc
}, {})


const SearchPlace = styled.div`
  position: relative;
  top: 20px;
`
const CardGrid = styled.div`
  margin: 40px 0;
  display: grid;
  grid-gap: 25px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-auto-rows: auto;
`

const cube = keyframes`
  0% {
    transform: translateY(0px) translateZ(0px) rotateX(0deg);
    transform-origin: top center;
    opacity: 0;
  }
  100% {
    transform: translateY(-10px) translateZ(1230px) rotateX(-35deg);
    transform-origin: top center;
    opacity: 1;
    box-shadow: inset 0 0 0 100px rgba(89, 108, 134, 0.7);
  }
`

const Bg = styled.img`
  position: absolute;
  height: 100%;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
`

const Cubwrapp = styled.section`
  position: relative;
  top: -100px;
  height: 300px;
  max-width: 50%;
  clip-path: polygon(15% 0%, 85% 0%, 100% 100%, 0% 100%);
  margin: auto;
  padding: 4em 0 2em;
  display: grid;
  grid-auto-columns: auto;
  grid-auto-rows: auto;
  justify-content: center;
  background: rgba(89, 108, 134, 0.9);
  color: white;
  animation: ${cube} 2.3s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;

  h3 {
    position: relative;
    top: -35px;
    padding: 0 50px;
    font-size: 19px;
    letter-spacing: 2px;
  }

  ${media.phone`
    clip-path: none;
    animation: none;
    max-width: 90%;
    border-top-left-radius: 50px;
    border-top-right-radius: 100px;
    border-bottom-right-radius: 70px;
    border-bottom-left-radius: 130px;
    overflow-x: hidden;
  `}
`

const Cubgrid = styled.div`
  position: relative;
  top: -220px;
  display: grid;
  justify-content: center; 
`

const Cardwrapp = styled.div`
  position: relative;
  width: 80%;
	margin-right: auto;
  margin-left: auto;
  margin-top: 20px;
  margin-bottom: 200px;
  display: grid;
  grid-gap: 25px;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  grid-auto-rows: auto;

  ${media.phone`left: -8px;`}
`

export default class Index extends Component {
  
  state = {
    posts: [],
  }

  componentDidMount() {
    this.setState({
      posts: this.props.data.allMarkdownRemark.edges,
    })
  }

  searchPosts = (e) => {
    let currentItems = []
    let searchingItem = []
    if (e.target.value !== '') {
      currentItems = this.props.data.allMarkdownRemark.edges
      searchingItem = currentItems.filter(({ node }) => {
        const Title = node.frontmatter.title.toLowerCase()
        const Tags = node.frontmatter.tags.toLowerCase()
        const filter = e.target.value.toLowerCase()
        let bool = false
        if (Title.includes(filter) || Tags.includes(filter)) {
          bool = true
        }
        return bool
      })
    } else {
      searchingItem = this.props.data.allMarkdownRemark.edges
    }
    this.setState({
      posts: searchingItem,
    })
  }

  render() {
    return (<>
      <Bg src={bg}/>

      <Layout >
    
        <Cardwrapp>
          <Card title='CMS' body='CMSs typically aim to avoid the need for hand coding but may support it for specific elements or entire pages.'/>
          <Card title='MVC' body='A PHP framework provides a basic structure for streamlining the development of web apps. We use them because they speed up the development process.'/>
          <Card title='MEAN' body='MEAN is a full-stack development toolkit used to develop a fast and robust web application.'/>
        </Cardwrapp>

  
        <Cubwrapp>
          <h2>Code Lover.</h2>
          <h3>Coffee. Coding. visiting. And much more.</h3>
          <Cubgrid> <Cube/> </Cubgrid>
        </Cubwrapp>

<h2>My Skills</h2>
       <CardGrid>
	   
            {this.state.posts.map(({ node }) => (
              <section key={node.id}>
                <BlogCard
                  title={node.frontmatter.title}
                  path={node.frontmatter.path}
                  tags={node.frontmatter.tags.split(' ')}
                  img={node.frontmatter.image}
                />
              </section>
            ))}
          </CardGrid>
		<Link  to="/blog"><h4>View More</h4></Link>
      </Layout>
    </>)
  }
}

export const index = graphql`
  query {
    allMarkdownRemark(limit:3
     sort: { order: DESC, fields: [frontmatter___date]}
     filter: {fileAbsolutePath: {regex: "/blog/.*\\.md$/"}}
   ){
       edges {
         node {
           id
           frontmatter {
             title
             date(formatString: "MMMM DD, YYYY")
             path
             image
             tags
           }
         }
       }
     }
 }
`
